import React from "react";

const DBBezier = () => (
  <svg className="graphic-dbbezier" width="50" height="50">
    <path d="M10 25Q25 -25 40 25 25 75 10 25" stroke="darkblue"
      fill="darkblue" transform="rotate(-45) translate(-25,10)" />
  </svg>
);

export default DBBezier; 



